
.button {
  border-radius: 10px;
  border-width: 0.5px;
  border: solid;
  border-color: #caa8ff;
  display: inline-block;
  text-align: center;
  color: #caa8ff;
  position: relative;
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
  padding: 15px 20px 15px 20px;
  -ms-flex-align: center;
      align-items: center;
  width: auto;
}

.button p {
  margin: auto;
}

.button:hover {
  color: #FFF;
  background-color: #caa8ff;
}



