.phone-container {
  position: relative;
  margin: auto;
  height: 400px;
  width: 210px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.iphone {
  position: absolute;
  height: 353px;
  width: 180px;
  top: 20px;
  border-radius: 22px;
  background: #FFF;
  border: 1.5px solid #D9D9D9;
}

.screen {
  position: absolute;
  height: 280px;
  left: 5px;
  right: 5px;
  top: 37.5px;
  background: #333;
  /*animation: colorchange 2.5s infinite;*/
}
.phone-button {
  position: absolute;
  height: 23px;
  width: 23px;
  left: 78px;
  bottom: 6px;
  border-radius: 50%;
  border: 1px solid #D9D9D9;
}
.speaker{
  position: absolute;
  height: 5px;
  width: 45px;
  left: 68px;
  top: 15px;
  border-radius: 18%;
  background: #D9D9D9;
}
.camera {
  position: absolute;
  height: 6px;
  width: 6px;
  top: 15px;
  left: 53px;
  border-radius: 50%;
  background: #D9D9D9;
}



